body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  padding: 18px;
}

form > div {
  padding: 16px;
}

input {
  padding: 10px;
}

input[type="submit"] {
  padding: 8px 20px;
}

.error-blck {
  margin: 10px 0;
  padding: 12px;
  background: #ffffff;
  color: #f84646;
  border: 1px solid #f84646;
}

.success-blck {
  margin: 10px 0;
  padding: 12px;
  background: #ffffff;
  color: #4a4acf;
  border: 1px solid #4a4acf;
}
